<template>
  <div>
    <p>水平线砌筑可以吗？</p>
    <p>如果您只是追求不均匀的砖状外观，那么水平砌体会更容易。</p>
    <p>如果你不关心粗糙的边缘，你甚至可以让东西漂浮起来。如果你想将其保留为一个块……</p>
    <p>带有 allowed 的 Flexboxflex-grow就是门票。</p>
    <div id="masonry">
      <v-card
        v-for="item in 30"
        :id="item"
        :key="item"
        color="primary"
      >
        {{ item }}
      </v-card>
    </div>
  </div>
</template>

<script>
  export default {

    mounted() {
      const numCols = 3;
      const colHeights = Array(numCols).fill(0);
      const container = document.getElementById('masonry');
      Array.from(container.children).forEach((child, i) => {
        const order = i % numCols;
        child.style.order = order;
        colHeights[order] += parseFloat(child.clientHeight);
      })
      container.style.height = Math.max(...colHeights) + 'px';
    },
    
  }
</script>

<style lang="scss" scoped>
#masonry {
  display: flex;
  flex-wrap: wrap;
  div {
    height: 180px;
    line-height: 180px;
    color: white;
    margin: 0 1rem 1rem 0;
    text-align: center;
    font-family: system-ui;
    font-weight: 900;
    font-size: 2rem;
    flex: 1 0 auto;
  } 
  @for $i from 1 through 36 { 
    div:nth-child(#{$i}) {
      $h: (random(400) + 70) + px;
      width: $h;
    }
  }
}

</style>